.skills-container {
    gap: 25px;

    .skills-title {
        font-size: 40px;
        color: var(--blue);
    }
    
    .skills-table {
        height: 100%;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 25px 10px;
    }

}

